<template>
<label >
	{{ title }}
	<input ref="pathValue"  v-model="pathValue"  type="text" >
</label>
</template>

<script>
	export default {
		name:'PathInput',
		props: {
			title:{
				type:String,
				default:'Введіть путь до файлу JS:'
			},
			currentPath: {
				type: String,
				default: ''
			},
			// eslint-disable-next-line vue/require-prop-types
			currentPathModifiers:{
				default:() => ({})
			}
		},
		computed:{
			pathValue:{
				get(){
					return this.currentPath
				},
				set(val){
					if(!/^\.\/(?:\w+\/)*\w+\.js$/.test(val) && this.currentPathModifiers.checkPath)
					{this.$refs.pathValue.style.backgroundColor = 'red'}
					this.$emit('update:currentPath', val)
				}
			}
		}
	}
</script>

<style lang="scss" scoped>

</style>